* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

button {
  border: none;
  outline: none;
  background: transparent;
}

.container {
  max-width: 1210px;
  padding: 0 15px;
  margin: 0 auto;
}

html,
body {
  overflow-x: hidden;
  min-height: 100vh;
}

body {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
}

.btn {
  background-color: #ff1a3b;
  border-radius: 5px;
  padding: 8px 30px;
  border: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  border: 2px solid #ff1a3b;
  transition: 0.2s;
}
.btn:hover {
  color: #ff1a3b;
  background-color: #fff;
}

.h2 {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 90%;
  text-transform: uppercase;
  color: #0e0f28;
  position: relative;
}
@media (max-width: 650px) {
  .h2 {
    font-size: 24px;
  }
}
.h2::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -18px;
  width: 80px;
  height: 5px;
  background-color: #ff1a3b;
}
@media (max-width: 650px) {
  .h2::after {
    width: 60px;
    height: 4px;
    bottom: -12px;
  }
}

.h2-center {
  text-align: center;
}
.h2-center::after {
  left: 0;
  right: 0;
  margin: auto;
}

.header {
  background: linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0.9) 100%);
  box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(10px);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 2;
  height: 90px;
}
@media (max-width: 1000px) {
  .header {
    position: fixed;
    width: 100%;
    z-index: 2;
    height: 49px;
  }
}
.header__top {
  display: flex;
  padding: 10px 3px 8px;
  height: 49px;
}
@media (max-width: 1000px) {
  .header__top {
    justify-content: space-between;
  }
}
.header__top-box {
  display: flex;
  margin-left: auto;
  gap: 30px;
}
@media (max-width: 1000px) {
  .header__top-box {
    display: none;
  }
}
.header__top-item {
  display: flex;
  align-items: center;
  gap: 10px;
}
.header__top-text {
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  color: #0e0f28;
  white-space: nowrap;
}
.header__top-text span {
  color: #ff1a3b;
}
.header__top-text--phone span {
  font-weight: 400;
  color: #0e0f28;
}
.header__burger {
  position: relative;
  width: 35px;
  height: 25px;
  cursor: pointer;
  display: none;
}
@media (max-width: 1000px) {
  .header__burger {
    display: block;
  }
}
.header__burger span {
  position: absolute;
  width: 100%;
  height: 5px;
  background-color: #000;
  border-radius: 3px;
  left: 0;
}
.header__burger span:nth-child(1) {
  top: 0;
}
.header__burger span:nth-child(2) {
  top: 0;
  bottom: 0;
  margin: auto;
}
.header__burger span:nth-child(3) {
  bottom: 0;
}
.header__bottom {
  padding: 7px 3px 6px;
  border-top: 1px solid rgba(14, 15, 40, 0.15);
}
@media (max-width: 1000px) {
  .header__bottom {
    padding: 0;
    border-top: none;
  }
}
.header__menu {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1000px) {
  .header__menu {
    position: fixed;
    top: 100%;
    left: 0;
    width: 100%;
    height: calc(100vh - 49px);
    flex-direction: column;
    background-color: #fff;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
    padding: 15px;
    transform: translateX(120%);
    transition: 0.5s;
    z-index: 100;
  }
  .header__menu.show {
    transform: translateX(0%);
  }
}
.header__menu-item {
  margin-left: 4.16vw;
}
@media (max-width: 1000px) {
  .header__menu-item {
    margin-left: 0;
  }
}
.header__menu-item:last-child {
  margin-left: 46px;
}
@media (max-width: 1000px) {
  .header__menu-item:last-child {
    margin-left: 0;
  }
}
.header__menu-item--1 {
  margin-left: 0;
}
@media (max-width: 1000px) {
  .header__menu-item--1 {
    padding-top: 15vh;
  }
}
@media (max-width: 1000px) {
  .header__menu-item--btn {
    padding-bottom: 15px;
  }
}
.header__menu-link {
  font-weight: 700;
  font-size: 10px;
  line-height: 12px;
  text-transform: uppercase;
  color: #0e0f28;
  transition: 0.2s;
}
@media (max-width: 1000px) {
  .header__menu-link {
    font-size: 15px;
    line-height: 1;
  }
}
.header__menu-link:hover {
  color: #ff1a3b;
}
.header__btn {
  padding: 5px 20px;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  border: 1px solid #ff1a3b;
}
@media (max-width: 1000px) {
  .header__btn {
    font-size: 15px;
    line-height: 1;
  }
}

.services {
  margin-top: 89px;
}
@media (max-width: 650px) {
