.modal {
  --transition: 0.2s;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  overflow: hidden;
  padding: 30px 0;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: visibility var(--transition), opacity var(--transition);
}

.modal.open {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
  overflow-y: auto;
  z-index: 100;
}

.modal__body {
  max-width: 500px;
  background-color: #fff;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  position: relative;
  border-radius: 4px;
  margin: auto;
  transition: var(--transition);
  transform: scale(0);
  pointer-events: none;
}

.modal__body.open {
  transform: scale(1);
  pointer-events: all;
}

.modal__btn {
  font-size: 24px;
  color: rgba(0, 0, 0, 0.678);
  line-height: 1;
  border: none;
  background: transparent;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal__btn:hover {
  color: #000;
}

.modal__content {
  padding: 30px;
}

