    font-size: 12px;
  }
}
.techserv-info__bottom path {
  fill: #0e0f28;
  fill-opacity: 0.3;
}

.useful-news {
  margin-top: 37px;
  margin-bottom: 155px;
}
@media (max-width: 600px) {
  .useful-news {
    margin-bottom: 50px;
  }
}
.useful-news .useful-cards {
  flex-wrap: wrap;
  gap: 30px 20px;
}

.article__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  text-transform: uppercase;
  color: #0e0f28;
  margin-bottom: 30px;
}
@media (max-width: 650px) {
  .article__title {
    font-size: 20px;
  }
}
@media (max-width: 450px) {
  .article__title {
    font-size: 14px;
  }
}
.article__title:nth-last-of-type(2) {
  margin-top: 65px;
  margin-bottom: 40px;
}
@media (max-width: 650px) {
  .article__title:nth-last-of-type(2) {
    margin-top: 50px;
  }
}
.article__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #0e0f28;
  margin-bottom: 23px;
}
@media (max-width: 650px) {
  .article__text {
    font-size: 14px;
  }
}
@media (max-width: 450px) {
  .article__text {
    font-size: 10px;
  }
}
.article__text:last-of-type {
  margin-bottom: 65px;
}
.article__images {
  margin: 57px 0;
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}
.article__images-item {
  flex: 0 1 calc(50% - 10px);
}
@media (max-width: 650px) {
  .article__images-item {
    flex: 0 1 100%;
  }
}
.article__images-img {
  max-width: 100%;
}

.footer-postnews {
  margin-top: 144px;
}
@media (max-width: 650px) {
  .footer-postnews {
    margin-top: 50px;
  }
}